<template>
  <div class="row" v-if="isReady">
    <div id="print-area">
      <div class="printer-box">
        <div class="flex-x-between header-print">
          <img src="/logo.png" alt="" />
          <p
            style="
              font-size: 2.7mm !important;
              padding: 1mm;
              border-right: 1px solid;
              margin-bottom: 0px !important;
            "
          >
            09975492181, 09941305965, Ground Floor, No.18, 58th st ဗဟိုလမ်း,
            Hlaing tsp, Yangon
          </p>

          <div>
            <p
              style="
                font-size: 2.7mm !important;
                padding: 1mm;
                margin-bottom: 0px !important;
              "
            >
              {{ $moment().format("DD/MM/YYYY") }}
            </p>
            <p
              style="
                font-size: 2.7mm !important;
                padding: 1mm;
                margin-bottom: 0px !important;
              "
            >
              ID : #{{ sale.id }}
            </p>
          </div>
        </div>
        <div class="print-content">
          <p><i class="bx bxs-user"></i> : {{ sale.customer_name }}</p>
          <p><i class="bx bxs-phone"></i> : {{ sale.phone }}</p>
          <p>
            <i class="bx bxs-map"></i> :
            <span v-if="sale.address.address">{{
              sale.address.address + " ,"
            }}</span>
            {{ formatString(sale.address.city) }},
            {{ formatString(sale.address.state) }}
          </p>
        </div>
        <div class="footer">
          <div class="flex-x-between">
            <span>Note : {{ sale.note || "" }}</span>
            <span>Delivery : {{ sale.delivery_company_name || "-" }}</span>
          </div>
          <span>Thanks for supporting my business 🖤</span>
        </div>
      </div>
    </div>
    <div class="col-12 mt-3">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              type="primary"
              circle
              class="no-width my-0 flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 mr-2 ml-2">{{ sale.sale_no }}</h5>
            <el-tag type="primary" class="rounded sm-hide mr-2">{{
              sale.status
            }}</el-tag>
            <el-tag
              type="success"
              class="rounded sm-hide mr-2"
              v-if="sale.payment_status == 'Paid'"
              ><i class="bx bx-check-circle"></i
              >{{ sale.payment_status }}</el-tag
            >

            <el-tag
              type="danger"
              class="rounded sm-hide mr-2"
              v-if="sale.payment_status == 'Unpaid'"
              ><i class="bx bx-time-five"></i>{{ sale.payment_status }}</el-tag
            >

            <el-tag
              type="warning"
              class="rounded sm-hide mr-2"
              v-if="sale.payment_status == 'Partially Paid'"
              ><i class="bx bx-time-five"></i>{{ sale.payment_status }}</el-tag
            >
            <el-tag
              type="success"
              v-if="sale.is_fulfilled"
              class="rounded sm-hide mr-2"
              >Fulfilled</el-tag
            >
            <el-tag type="warning" v-else class="rounded sm-hide mr-2">
              <i class="bx bx-time-five"></i> Unfulfilled</el-tag
            >
          </div>
          <div class="flex-align-center sm-hide flex-end">
            <el-button
              v-if="startEditing && !gettingSale"
              @click="discardEditingSale"
              type="primary"
              v-ripple.400
              class="flat"
            >
              Discard
            </el-button>
            <el-button
              v-if="startEditing && !gettingSale"
              :disabled="!startEditing"
              :loading="saving"
              v-ripple.400
              @click="Put"
              type="primary"
            >
              Save
            </el-button>
            <el-button
              v-ripple.400
              type="primary"
              class="mr-2"
              @click="updateStatus('Completed')"
            >
              Mark as Completed
            </el-button>

            <el-dropdown
              @command="changeActions"
              split-button
              type="primary"
              trigger="click"
            >
              More actions

              <el-dropdown-menu slot="dropdown" class="button-dropdown">
                <el-dropdown-item command="Canceled">
                  <i class="bx bx-x mr-2"></i>Cancel order</el-dropdown-item
                >
                <el-dropdown-item command="Archived">
                  <i class="bx bx-box mr-2"></i> Archive</el-dropdown-item
                >
                <el-dropdown-item command="Print">
                  <i class="bx bx-box mr-2"></i> Print Address</el-dropdown-item
                >
                <el-dropdown-item
                  command="pdf"
                  v-if="store.type !== 'supplier'"
                >
                  <i class="bx bx-printer mr-2"></i> Export
                  voucher</el-dropdown-item
                >
                <el-dropdown-item
                  command="printCode"
                  v-if="store.type == 'supplier'"
                >
                  <i class="bx bx-printer mr-2"></i> Export
                  Code</el-dropdown-item
                >
              </el-dropdown-menu>
            </el-dropdown>
          </div>
        </div>
        <div class="col-12 lg-hide mb-3 md-hide flex-align-center">
          <el-tag type="primary" class="rounded mr-2">{{ sale.status }}</el-tag>
          <el-tag
            type="success"
            class="rounded mr-2"
            v-if="sale.payment_status == 'Paid'"
            ><i class="bx bx-check-circle"></i>{{ sale.payment_status }}</el-tag
          >

          <el-tag
            type="danger"
            class="rounded mr-2"
            v-if="sale.payment_status == 'Unpaid'"
            ><i class="bx bx-time-five"></i>{{ sale.payment_status }}</el-tag
          >

          <el-tag
            type="warning"
            class="rounded mr-2"
            v-if="sale.payment_status == 'Partially Paid'"
            ><i class="bx bx-time-five"></i>{{ sale.payment_status }}</el-tag
          >
          <el-tag type="success" v-if="sale.is_fulfilled" class="rounded mr-2"
            >Fulfilled</el-tag
          >
          <el-tag type="warning" v-else class="rounded mr-2">
            <i class="bx bx-time-five"></i> Unfulfilled</el-tag
          >
        </div>
        <div class="col-lg-8 col-md-cu-8 pr-lg-0">
          <div class="box">
            <div class="flex-x-between">
              <p class="mt-0 mb-2 text-bold">Items</p>

              <div class="d-flex align-items-center">
                <p
                  @click="openFulfill"
                  class="mt-0 mr-2 pointer sm-hide primary hover-underline"
                >
                  Add fulfillment
                </p>
                <p
                  @click="addItem"
                  class="mt-0 pointer primary hover-underline"
                >
                  <i class="bx bx-plus mr-1"></i>Add item
                </p>
              </div>
            </div>
            <div class="row">
              <div class="col-12" v-if="store.type == 'normal'">
                <el-select
                  v-model="productQuery"
                  filterable
                  remote
                  reserve-keyword
                  class="w-100 my-3 my-sm-2"
                  placeholder="search product"
                  :remote-method="getProducts"
                  :loading="loadingProduct"
                >
                  <el-option
                    v-for="product in products"
                    :key="product.id"
                    :label="product.name + ' / ' + product.barcode || ''"
                    :value="product"
                  >
                  </el-option>
                </el-select>
              </div>

              <div
                class="col-12"
                v-loading="loadingProduct"
                v-if="sale.products.length > 0"
              >
                <el-collapse>
                  <el-collapse-item
                    v-for="(product, index) in sale.products"
                    :key="index"
                    :name="index"
                  >
                    <template slot="title">
                      <div class="flex-x-between w-100 collapse-header">
                        <div class="flex-align-center">
                          <p class="mr-3">
                            No.{{ index + 1 }} / {{ product.name }} -
                            {{ product.subtotal
                            }}{{ store.type === "supplier" ? "¥" : "MMK" }}
                          </p>
                          <p
                            class="primary-flat sm-hide"
                            v-if="product.link && store.type == 'supplier'"
                            style="
                              background: rgba(var(--success), 0.2) !important;
                              color: rgba(var(--success), 1) !important;
                              height: 20px;
                              padding: 0px 5px;
                              line-height: 20px;
                            "
                          >
                            Has Code
                          </p>
                          <p
                            class="danger-flat sm-hide"
                            v-if="!product.link && store.type == 'supplier'"
                            style="
                              height: 20px;
                              padding: 0px 5px;
                              line-height: 20px;
                              border-radius: 4px;
                            "
                          >
                            No Code
                          </p>
                          <p
                            class="primary-flat ml-3 sm-hide"
                            v-if="isFulfilled(product)"
                            style="
                              background: rgba(var(--success), 0.2) !important;
                              color: rgba(var(--success), 1) !important;
                              height: 20px;
                              padding: 0px 5px;
                              line-height: 20px;
                            "
                          >
                            Fulfilled on
                            {{ $moment(product.date).format("DD/MM/YYYY") }}
                          </p>
                        </div>
                        <p
                          @click="removeProduct(index, product.id)"
                          class="danger hover-underline"
                          style="margin-right: 8px; z-index: 10"
                        >
                          <i class="bx bx-x"></i>
                        </p>
                      </div>
                    </template>
                    <div class="row mb-3">
                      <div class="col-12 mb-3">
                        <label for="Media" class="custom-label">Media</label>
                        <file
                          :key="index + product.name"
                          @completed="setImage($event, index)"
                          :initialData="
                            product.image ? [{ image: product.image }] : []
                          "
                        />
                      </div>
                      <div class="col-12 mb-3">
                        <label for="Name" class="custom-label">Name</label>
                        <el-input
                          class="w-100"
                          name="Name"
                          v-model="product.name"
                        ></el-input>
                      </div>

                      <div class="col-12 mb-3" v-if="store.type == 'supplier'">
                        <label for="Code" class="custom-label"
                          >Tracking Code</label
                        >
                        <el-input
                          class="w-100"
                          name="Code"
                          v-model="product.link"
                        ></el-input>
                      </div>

                      <div
                        class="col-lg-6 mb-3"
                        v-if="store.type !== 'supplier'"
                      >
                        <label for="Link" class="custom-label"
                          >Capital price(MMK)</label
                        >
                        <el-input
                          class="w-100"
                          name="Capital price(MMK)"
                          @change="calcPrice(index)"
                          type="number"
                          v-model="product.primary_price_myanmar"
                        ></el-input>
                      </div>
                      <div
                        class="col-lg-6 mb-3"
                        v-if="store.type !== 'supplier'"
                      >
                        <label for="Link" class="custom-label"
                          >Selling Price(MMK)</label
                        >
                        <el-input
                          class="w-100"
                          name="Sale Price(MMK)"
                          @change="calcPrice(index)"
                          type="number"
                          v-model="product.sale_price"
                        ></el-input>
                      </div>

                      <div class="col-lg-6">
                        <div class="row">
                          <div class="col-6">
                            <label for="Link" class="custom-label"
                              >Quantity</label
                            >
                            <el-input-number
                              :min="0"
                              class="w-100"
                              controls-position="right"
                              v-model="product.quantity"
                              @change="changeQuantity(index)"
                            ></el-input-number>
                          </div>
                          <div class="col-6">
                            <label for="Link" class="custom-label"
                              >Fulfilled</label
                            >
                            <el-input-number
                              :min="0"
                              class="w-100"
                              controls-position="right"
                              v-model="product.number_of_fullfilled"
                            ></el-input-number>
                          </div>
                        </div>
                      </div>
                      <div
                        class="col-lg-6 mb-3"
                        v-if="store.type == 'supplier'"
                      >
                        <label for="Link" class="custom-label">Price</label>
                        <el-input
                          class="w-100"
                          name="Price"
                          @change="calcPrice(index)"
                          type="number"
                          v-model="product.sale_price"
                        ></el-input>
                      </div>
                      <div
                        v-if="store.type !== 'supplier'"
                        class="
                          d-flex
                          justify-content-around
                          col-lg-6
                          align-items-center
                        "
                      >
                        <div
                          class="
                            d-flex
                            justify-content-center
                            align-items-center
                            flex-column
                          "
                        >
                          <p class="md-text">Margin</p>
                          <p class="mt-2">
                            {{ product.margin ? product.margin + "%" : "-" }}
                          </p>
                        </div>
                        <div
                          class="
                            d-flex
                            justify-content-center
                            align-items-center
                            flex-column
                          "
                        >
                          <p class="md-text">Profit(MMK)</p>
                          <p class="mt-2">
                            {{ product.profit ? product.profit : "-" }}
                          </p>
                        </div>
                      </div>
                    </div>
                  </el-collapse-item>
                </el-collapse>
              </div>
              <el-empty class="w-100" description="No item found!" v-else>
                <template #image> <empty /> </template
              ></el-empty>
            </div>
          </div>

          <div class="box mt-3">
            <div class="row">
              <div class="col-12">
                <p class="mt-0 mb-2 text-bold">Pricing</p>
                <div class="flex-x-between">
                  <p
                    class="primary hover-underline pointer"
                    @click="openDiscount = !openDiscount"
                  >
                    Add discount
                  </p>

                  <p class="text-right">
                    <span v-if="store.type == 'supplier'"> ¥ </span>
                    {{ parseFloat(sale.discount) === 0 ? "-" : sale.discount }}
                    <span v-if="store.type !== 'supplier'"> MMK </span>
                    <span v-if="sale.discount_reason" class="sm-text">
                      <br />
                      {{ sale.discount_reason }}
                    </span>
                  </p>
                </div>
                <el-divider></el-divider>
                <div class="flex-x-between">
                  <p class="">Subtotal</p>
                  <p class="">
                    <span v-if="store.type == 'supplier'"> ¥ </span>
                    {{ parseFloat(sale.subtotal) === 0 ? "-" : sale.subtotal }}
                    <span v-if="store.type !== 'supplier'"> MMK </span>
                  </p>
                </div>
                <el-divider></el-divider>
                <div class="flex-x-between" v-if="store.type === 'supplier'">
                  <p class="">Supplier percentage</p>
                  <p class="">
                    <span v-if="store.type == 'supplier'"> ¥ </span>
                    {{
                      parseFloat(sale.subtotal) === 0
                        ? "-"
                        : (
                            parseFloat(sale.subtotal) *
                            (parseFloat(sale.supplier_percentage) / 100)
                          ).toFixed(2)
                    }}
                  </p>
                </div>
                <el-divider v-if="store.type === 'supplier'"></el-divider>
                <div class="flex-x-between">
                  <p class="">Total</p>
                  <p class="">
                    <span v-if="store.type == 'supplier'"> ¥ </span>
                    {{ parseFloat(sale.total) === 0 ? "-" : sale.total
                    }}<span v-if="store.type !== 'supplier'"> MMK </span>
                  </p>
                </div>

                <el-divider v-if="store.type === 'supplier'"></el-divider>
                <div class="flex-x-between" v-if="store.type === 'supplier'">
                  <p class="">Purchased Amount</p>
                  <el-input
                    class=""
                    style="width: 150px"
                    @change="calcProfitAmount"
                    v-model="sale.purchase_amount"
                  ></el-input>
                </div>

                <el-divider v-if="store.type === 'supplier'"></el-divider>
                <div class="flex-x-between" v-if="store.type === 'supplier'">
                  <p class="">Profit Amount</p>
                  <p class="">
                    <span> ¥ </span>
                    {{
                      parseFloat(sale.profit_amount) === 0
                        ? "-"
                        : sale.profit_amount
                    }}
                  </p>
                </div>
              </div>
            </div>
          </div>

          <div class="box mt-3 mb-3">
            <div class="flex-x-between mb-3">
              <p class="mt-0 mb-2 text-bold">Payments</p>

              <div class="d-flex align-items-center">
                <p
                  class="primary hover-underline pointer"
                  @click="openPaymentDialog"
                >
                  Add payment
                </p>
              </div>
            </div>

            <div class="row">
              <div
                class="col-12"
                v-for="payment in sale.payments"
                :key="payment.id"
              >
                <div class="d-flex justify-content-between align-items-center">
                  <p>
                    Paid on
                    {{ payment.date }}
                  </p>
                  <div class="d-flex align-items-center">
                    <span v-if="store.type == 'supplier'"> ¥ </span>
                    <p>{{ payment.amount }}</p>
                    <span v-if="store.type !== 'supplier'"> MMK </span>
                  </div>
                </div>
                <div class="b-divider"></div>
              </div>
              <div class="col-12">
                <div class="d-flex justify-content-between align-items-center">
                  <p>Due amount</p>
                  <p>
                    <span v-if="store.type == 'supplier'"> ¥ </span>
                    {{ sale.due_amount
                    }}<span v-if="store.type !== 'supplier'"> MMK </span>
                  </p>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="col-lg-4 col-md-cu-4 mb-3">
          <div class="box">
            <label for="" class="custom-label md-text">Date of order</label>
            <el-date-picker
              class="w-100"
              v-model="sale.date"
              type="date"
              format="dd/MM/yyyy"
              value-format="dd/MM/yyyy"
              placeholder="Pick a date"
            >
            </el-date-picker>

            <label
              for=""
              v-if="store.type !== 'supplier'"
              class="custom-label mt-3 md-text"
              >Type</label
            >
            <div class="flex-align-center" v-if="store.type !== 'supplier'">
              <el-radio v-model="sale.type" class="mr-3" label="In Stock"
                >In Stock</el-radio
              >
              <el-radio v-model="sale.type" label="Pre Order"
                >Pre Order</el-radio
              >
            </div>

            <label for="" class="custom-label mt-3 md-text">Exported By</label>
            <el-select
              v-model="sale.exported_by"
              filterable
              remote
              reserve-keyword
              disabled
              class="w-100"
              placeholder="Select"
            >
            </el-select>

            <label for="" class="custom-label mt-3 md-text">Delivery</label>
            <el-select
              v-model="sale.delivery_company_name"
              filterable
              remote
              reserve-keyword
              class="w-100"
              allow-create
              placeholder="Select or create new"
              :remote-method="getDeliveries"
              :loading="loadingDelivey"
            >
              <el-option
                v-for="delivery in deliveries"
                :key="delivery.id"
                :label="delivery.name"
                :value="delivery.name"
              >
              </el-option>
            </el-select>

            <label class="custom-label mt-3 mb-0">Note</label>
            <el-input
              type="textarea"
              placeholder="Text such as facebook profile link, etc."
              :row="5"
              v-model="sale.note"
            ></el-input>
          </div>

          <div class="box mt-3" v-if="store.type == 'supplier'">
            <p class="mt-0 mb-2 text-bold">Pricing Details</p>
            <label class="custom-label">Exchange Rate</label>
            <el-input class="w-100" v-model="sale.money_price"></el-input>

            <label class="custom-label mt-3">Service Fees(%)</label>
            <el-input
              class="w-100"
              @change="calcTotal"
              v-model="sale.supplier_percentage"
            ></el-input>

            <label class="custom-label mt-3 mb-0">Order Link</label>
            <el-input id="orderLink" v-model="linkUrl"></el-input>
            <div class="flex-align-start mt-2">
              <el-button class="flat mr-2" @click="copyURL" type="primary"
                >Copy</el-button
              >
              <el-button class="ml-2" @click="openUrl" type="primary"
                >Open Link</el-button
              >
            </div>
          </div>

          <div class="box mt-3 mb-3">
            <p class="mt-0 mb-2 text-bold">Customer</p>
            <label for="" class="custom-label md-text">Name</label>
            <el-select
              v-model="sale.customer_name"
              @change="changeCustomer"
              filterable
              data-is-search="true"
              remote
              allow-create
              reserve-keyword
              class="w-100 mb-3"
              placeholder="Select or create new"
              :remote-method="getCustomers"
              :loading="loadingCustomer"
            >
              <el-option
                v-for="customer in customers"
                :key="customer.id"
                :label="customer.name"
                :value="customer.name"
              >
              </el-option>
            </el-select>

            <label for="" class="custom-label md-text">Phone</label>
            <el-input
              class="w-100 mb-3"
              placeholder="Phone number"
              v-model="sale.phone"
            ></el-input>

            <label for="" class="custom-label md-text">State & city</label>
            <el-cascader
              v-model="address"
              :options="options"
              @change="handleChangeAddress"
              value="item"
            ></el-cascader>
            <label for="Street address" class="mt-3 custom-label"
              >Street address</label
            >
            <el-input
              class="w-100"
              type="textarea"
              :row="5"
              name="Street address"
              placeholder="Street address"
              v-model="sale.address.address"
            ></el-input>
          </div>
        </div>

        <div class="col-12 lg-hide md-hide">
          <!-- <el-button
            v-ripple.400
            type="primary"
            class="mx-0 mb-3 w-100"
            @click="updateStatus('Completed')"
          >
            Mark as Completed
          </el-button> -->

          <el-button
            v-ripple.400
            type="primary"
            :loading="showVoucher"
            class="mx-0 mb-3 w-100"
            @click="changeActions('pdf')"
          >
            Export Voucher
          </el-button>
          <el-button
            v-if="startEditing && !gettingSale"
            :disabled="!startEditing"
            :loading="saving"
            v-ripple.400
            class="w-100 mx-0 mb-3"
            @click="Put"
            type="primary"
          >
            Save
          </el-button>
          <el-button
            v-if="startEditing && !gettingSale"
            @click="discardEditingSale"
            type="primary"
            v-ripple.400
            class="flat w-100 mx-0 mb-3"
          >
            Discard
          </el-button>
        </div>
      </div>
    </div>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openDiscount"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Add discount</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <div class="row">
          <div class="col-6 pr-1">
            <label for="discountType" class="pt-1 custom-label"
              >Discount type</label
            >
            <el-select
              v-model="sale.discount_type"
              class="w-100"
              name="discountType"
            >
              <el-option label="Amount" value="$"> </el-option>
              <el-option label="Percentage" value="%"> </el-option>
            </el-select>
          </div>
          <div class="col-6 pl-1">
            <label for="discountType" class="pt-1 custom-label"
              >Discount value</label
            >
            <el-input type="number" class="w-100" v-model="discount"></el-input>
          </div>
          <div class="col-12">
            <label for="reason" class="pt-1 custom-label">Reason</label>
            <el-input
              type="textarea"
              :row="3"
              class="w-100"
              name="reason"
              v-model="sale.discount_reason"
            ></el-input>
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="openDiscount = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            @click="calcDiscount"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openPayment"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Make a payment</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <div class="row">
          <div class="col-6 pr-1">
            <div class="w-100">
              <label for="" class="custom-label mt-1 md-text">Date</label>
              <el-date-picker
                class="w-100"
                v-model="payment.date"
                type="date"
                format="dd/MM/yyyy"
                value-format="dd/MM/yyyy"
                placeholder="Pick a date"
              >
              </el-date-picker>
            </div>
          </div>
          <div class="col-6 pl-1">
            <label for="" class="custom-label mt-1 md-text">Amount</label>
            <el-input
              class="w-100"
              label=""
              placeholder="0"
              type="number"
              v-model="payment.amount"
            />
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="openPayment = false"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400="'rgba(var(--primary),0.2)'"
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            :loading="adding"
            @click="addPayment"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      :visible.sync="openFulfillDialog"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Fulfill items</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>
      <div class="con-content">
        <div class="row" v-for="(tr, index) in sale.products" :key="tr.id">
          <div class="col-8 flex-x-between">
            <div class="flex-align-center">
              <img
                v-lazy="mediaUrl + tr.image"
                class="simple-image list-image"
                alt=""
              />
              <p>
                {{ tr.name }}
              </p>
            </div>
          </div>
          <div class="col-4 flex-align-center flex-end">
            <el-input-number
              class="w-50"
              v-model="tr.number_of_fullfilled"
              type="number"
              @change="changeFulfill(index)"
              :min="0"
              :max="parseFloat(tr.quantity)"
              controls-position="right"
            ></el-input-number>
            <p class="ml-2">of {{ tr.quantity }}</p>
          </div>
          <div class="col-12" v-if="index !== sale.products.length - 1">
            <div class="b-divider"></div>
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="cancelFulfill"
            class="my-0 flat mr-0"
            type="primary"
            v-ripple.400="'rgba(var(--primary),0.2)'"
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            :loading="addingFulfill"
            @click="addFulfill"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>
    <voucher :sale="sale" v-if="showVoucher" />
  </div>
</template>

<script>
import { mapState } from "vuex";
import address from "../../../core/constant/address";
import { url, mediaUrl } from "../../../core/constant/data";
import { file, empty, voucher } from "../../common";
import domtoimage from "dom-to-image";
export default {
  data() {
    return {
      url: url,
      options: address,
      openCustomProduct: false,
      mediaUrl: mediaUrl,
      openFulfillDialog: false,
      addingFulfill: false,
      openPayment: false,
      payment: {
        amount: "0",
        date: "",
        sale_id: null,
      },
      customProduct: {
        name: "",
        is_custom_product: true,
        quantity: "1",
        price: "0",
        number_of_stock: 100000,
        image: "images/default.png",
        variation_product: null,
        product: null,
        subtotal: "0",
      },
      customers: [],
      deliveries: [],
      inventory: [],
      removed_ids: [],
      loadingProduct: false,
      products: [],
      loadingCustomer: false,
      loadingDelivey: false,
      address: [],
      tax: 5,
      discount: 0,
      selectedProduct: "",
      openDiscount: false,
      saving: false,
      gettingSale: false,
      sale: {
        payment_status: "Unpaid",
        supplier_percentage: "5",
        money_price: "300",
        customer_name: "",
        location_name: "",
        type: "Instock",
        exported_by: "",
        status: "Active",
        tax_rate: 5,
        delivery_company_name: null,
        delivery_date: null,
        payment_method: null,
        location: 1,
        payments: [],
        address: { state: "", city: "", address: "", map: "" },
        products: [],
        timelines: [],
        subtotal: "",
        total: "0",
        discount: "0",
        discount_reason: "",
        discount_type: "$",
        is_same_address: false,
        tax: "",
        note: "",
        tags: [],
      },
      selectedCustomer: {},
      loading: null,
      startEditing: false,
      adding: false,
      due_amount: 0,
      query: "",
      voucher: {
        type: "pdf",
      },
      generating: false,
      linkUrl: "",
      showVoucher: false,
      productQuery: {},
    };
  },
  components: { file, empty, voucher },
  watch: {
    sale: {
      handler: function () {
        this.startEditing = true;
      },
      deep: true,
    },
    productQuery(val) {
      this.sale.products.push({
        quantity: "1",
        sale_price: val.price,
        subtotal: val.price,
        name: val.name,
        image: val.image,
        primary_price: "0",
        primary_price_myanmar: val.cost_per_item,
        number_of_fullfilled: 0,
        product_id: val.id,
      });
    },
    locations: {
      handler: function (value) {
        this.sale.location = value[0].id;
        this.sale.location_name = value[0].name;
      },
      deep: true,
    },
  },
  mounted() {
    this.$store.commit("setIsReady", false);
    this.filterSale();
  },
  beforeMount() {
    window.addEventListener("beforeunload", this.preventNav);
    this.$once("hook:beforeDestroy", () => {
      window.removeEventListener("beforeunload", this.preventNav);
    });

    document.addEventListener("scan", this.onScan);
    this.$once("hook:beforeDestroy", () => {
      document.removeEventListener("scan", this.onScan);
    });
  },
  beforeDestroy() {
    document.removeEventListener("scan", this.onScan);
  },
  beforeRouteLeave(to, from, next) {
    document.removeEventListener("scan", this.onScan);
    if (this.startEditing) {
      this.$smalltalk
        .confirm(
          "Question",
          "Do you really want to leave? you have unsaved changes!"
        )
        .then(() => {
          next();
        })
        .catch(() => {
          next(false);
        });
    } else {
      next();
    }
  },
  methods: {
    onScan({ detail }) {
      this.loadingProduct = true;
      if (detail.scanCode) {
        this.$axios
          .get("product/search/?query=" + detail.scanCode)
          .then((response) => {
            this.loadingProduct = false;

            if (response.data.length > 0) {
              var val = response.data[0];
              var check = this.sale.products.filter(
                (item) => item.product_id == val.id
              );

              if (check.length > 0) {
                this.sale.products.map((item) => {
                  if (item.product_id == val.id) {
                    item.quantity = item.quantity + 1;
                  }
                });
              } else {
                this.sale.products.push({
                  quantity: "1",
                  sale_price: val.price,
                  subtotal: val.price,
                  name: val.name,
                  image: val.image,
                  primary_price: "0",
                  primary_price_myanmar: val.cost_per_item,
                  number_of_fullfilled: 0,
                  product_id: val.id,
                });
              }
            } else {
              this.$noti.display({
                message: "No products found with " + detail.scanCode,
                title: "",
                status: "success",
                color: "error",
              });
            }
          })
          .catch((err) => {
            this.loadingProduct = false;
            this.$noti.display({
              message: err,
              title: "",
              status: "error",
              color: "error",
            });
          });
      }
    },
    formatString(str) {
      var str_ = str.split("_").join(" ");
      str_ = str_.split("(").pop().replace(")", "");
      return str_;
    },
    preventNav(event) {
      if (!this.startEditing) return;
      event.preventDefault();
      event.returnValue = "";
    },
    openUrl() {
      window.open(this.linkUrl, "_blank").focus();
    },
    copyURL() {
      navigator.clipboard.writeText(this.linkUrl);
      this.isCopied = true;
      this.$noti.display({
        color: "success",
        message: "Copied to clipboard!",
      });
    },
    handleChangeAddress(address) {
      this.sale.address.state = address[0];
      this.sale.address.city = address[1];
    },
    openPaymentDialog() {
      this.payment.date = this.$moment().format("DD/MM/YYYY");
      this.payment.amount = this.sale.due_amount;
      this.openPayment = true;
    },
    changeActions(command) {
      if (command == "image" || command == "pdf") {
        this.voucher.type = command;
        this.generate();
      } else if (command == "Print") {
        window.print();
      } else if (command == "printCode") {
        this.printPdf();
      } else {
        this.updateStatus(command);
      }
    },
    updateStatus(status) {
      this.updatingStatus = true;
      this.$axios
        .put("sales-status/", {
          status: status,
          ids: [this.sale.id],
        })
        .then(() => {
          this.sale.status = status;
          this.saleBeforeEdit = JSON.stringify(this.sale);
          setTimeout(() => {
            this.editingCustomer = false;
            this.startEditing = false;
            this.updatingStatus = false;
          }, 10);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
          this.updatingStatus = false;
        });
    },
    printPdf() {
      var tableRows = "";
      var data = [];
      this.sale.products.forEach((element) => {
        if (element.link) {
          var check = data.filter((da) => da.link == element.link);
          if (check.length == 0) {
            data.push(element);
          }
        }
      });

      data.forEach((order, index) => {
        tableRows += `<tr>
                          <td>${index + 1}</td>
                          <td>${order.link}</td>
                          <td>${order.created_at}</td>
                      </tr>`;
      });
      var html = `
                <!DOCTYPE html>
                <html lang="en">
                  <head>
                    <meta charset="UTF-8" />
                    <meta http-equiv="X-UA-Compatible" content="IE=edge" />
                    <meta name="viewport" content="width=device-width, initial-scale=1.0" />
                    <title>Order Codes</title>
                    <style>
                      table {
                        font-family: arial, sans-serif;
                        border-collapse: collapse;
                        width: 100%;
                        margin-bottom: 15px;
                      }

                      td,
                      th {
                        border: 1px solid #dddddd;
                        text-align: left;
                        padding: 8px;
                      }
                    </style>
                  </head>
                  <body>
                    <table>
                      <thead>
                        <tr>
                          <th style="width:50px;">No.</th>
                          <th>Code</th>
                          <th>Date</th>
                        </tr>
                      </thead>
                      <tbody id="table-body">${tableRows}</tbody>
                    </table>
                  </body>
                </html>
`;
      var newWin = window.open("", "Print-Window");
      newWin.document.open();
      newWin.document.write(html);
      newWin.window.print();
    },
    isFulfilled(product) {
      return (
        parseFloat(product.quantity) ===
        parseFloat(product.number_of_fullfilled)
      );
    },
    getProducts(query) {
      this.loadingProduct = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("product/search/?query=" + query)
        .then((response) => {
          this.loadingProduct = false;
          this.products = response.data;
        })
        .catch((err) => {
          this.loadingProduct = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    changeFulfill(index) {
      this.sale.products[index].is_fulfilled =
        parseFloat(this.sale.products[index].quantity) ===
        parseFloat(this.sale.products[index].number_of_fullfilled);
    },
    addFulfill() {
      this.addingFulfill = true;
      var data = [];
      var is_fulfilled = true;
      this.sale.products.forEach((value) => {
        value.is_fulfilled =
          parseFloat(value.quantity) === parseFloat(value.number_of_fullfilled);
        data.push({
          is_fulfilled: value.is_fulfilled,
          number_of_fullfilled: value.number_of_fullfilled,
          id: value.id,
        });
        if (!value.is_fulfilled) {
          is_fulfilled = value.is_fulfilled;
        }
      });
      this.sale.is_fulfilled = is_fulfilled;
      this.$axios
        .put("fulfill/", { data, sale_id: this.sale.id, is_fulfilled })
        .then(() => {
          this.addingFulfill = false;
          this.openFulfillDialog = false;
          this.saleProductsBeforeEdit = JSON.stringify(this.sale.products);
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! Fulfillment has been successfully added.",
          });
        })
        .catch((err) => {
          this.addingFulfill = false;
          this.openFulfillDialog = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    openFulfill() {
      this.saleProductsBeforeEdit = JSON.stringify(this.sale.products);
      this.openFulfillDialog = true;
    },
    cancelFulfill() {
      this.sale.products = JSON.parse(this.saleProductsBeforeEdit);
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
      this.openFulfillDialog = false;
    },
    addPayment() {
      this.adding = true;
      this.payment.sale_id = this.sale.id;
      this.payment.payment_status = this.sale.payment_status;
      this.$axios
        .post("payment/", {
          data: this.payment,
        })
        .then((response) => {
          this.sale.payments.push(response.data);
          this.calcDueAmount();
          this.payment = {
            sale_id: this.sale.id,
            amount: "",
            date: this.$moment().format("dd/MM/yyyy"),
          };

          setTimeout(() => {
            this.Put();
          }, 10);
        })
        .catch((err) => {
          this.openPayment = false;
          this.adding = false;
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });
    },
    filterSale() {
      this.gettingSale = true;
      this.$axios
        .get("sale/?sid=" + this.$route.params.id)
        .then((response) => {
          this.sale = response.data;
          this.linkUrl = `https://osassist-orders.netlify.app/?sid=${this.store.id}&oid=${response.data.id}&date=${response.data.date}`;
          this.saleBeforeEdit = JSON.stringify(this.sale);
          var is_fulfilled = true;
          this.address = [
            response.data.address.state,
            response.data.address.city,
          ];
          this.sale.products.forEach((product) => {
            product.is_fulfilled =
              parseFloat(product.quantity) ===
              parseFloat(product.number_of_fullfilled);
            if (!product.is_fulfilled) {
              is_fulfilled = false;
            }
          });
          this.sale.is_fulfilled = is_fulfilled;
          this.calcDueAmount();
          setTimeout(() => {
            this.startEditing = false;
            this.gettingSale = false;
          }, 10);
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.$noti.display({
            message: err,
            status: "error",
            color: "error",
          });
        });

      this.calcDueAmount();
    },
    calcDueAmount() {
      this.sale.due_amount =
        parseFloat(this.sale.total) -
        parseFloat(
          this.sale.payments.reduce((total, item) => {
            return total + parseFloat(item.amount);
          }, 0)
        );
      if (parseFloat(this.sale.due_amount) === 0) {
        this.sale.payment_status = "Paid";
      } else if (
        parseFloat(this.sale.due_amount) === parseFloat(this.sale.total)
      ) {
        this.sale.payment_status = "Unpaid";
      } else {
        this.sale.payment_status = "Partially Paid";
      }
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
    },
    setImage($event, index) {
      if ($event.length > 0) {
        this.sale.products[index].image = $event[0].image;
      }
    },
    addItem() {
      this.sale.products.push({
        quantity: "1",
        sale_price: "",
        subtotal: "",
        name: "",
        image: "/media/default.png",
        link: null,
        primary_price: null,
        primary_price_myanmar: null,
        number_of_fullfilled: 0,
        product_id: "",
      });
      this.calcSubtotal();
    },
    removeProduct(index, id) {
      this.sale.products.splice(index, 1);
      this.removed_ids.push(id);
      this.calcSubtotal();
    },
    getCustomers(query) {
      this.loadingCustomer = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-customer/?query=" + query)
        .then((response) => {
          this.loadingCustomer = false;
          this.customers = response.data;
        })
        .catch((err) => {
          this.loadingCustomer = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    getDeliveries(query) {
      this.loadingDelivery = true;
      if (query === "" || !query) {
        query = "Null";
      }
      this.$axios
        .get("search-delivery/?query=" + query)
        .then((response) => {
          this.loadingDelivery = false;
          this.deliveries = response.data;
        })
        .catch((err) => {
          this.loadingDelivery = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    changeCustomer() {
      var check = this.customers.filter((data) => {
        return data.name == this.sale.customer_name;
      });
      if (check.length > 0) {
        this.selectedCustomer = check[0];
        this.sale.phone = this.selectedCustomer.phone;
        if (this.selectedCustomer.address) {
          this.sale.address = this.selectedCustomer.address;
          this.address = [
            this.selectedCustomer.address.state,
            this.selectedCustomer.address.city,
          ];
        }
      }
    },
    calcDiscount() {
      if (this.sale.discount_type === "%") {
        this.sale.discount =
          (parseFloat(this.discount) / 100) * parseFloat(this.sale.subtotal);
      } else {
        this.sale.discount = parseFloat(this.discount);
      }
      this.openDiscount = false;
      this.calcTotal();
    },
    calcTotal() {
      if (this.store.type === "supplier") {
        this.sale.total = (
          parseFloat(this.sale.subtotal) +
          parseFloat(this.sale.subtotal) *
            (parseFloat(this.sale.supplier_percentage) / 100) -
          parseFloat(this.sale.discount)
        ).toFixed(0);

        this.calcProfitAmount();
      } else {
        this.sale.total = (
          parseFloat(this.sale.subtotal) - parseFloat(this.sale.discount)
        ).toFixed(0);
      }
    },

    calcSubtotal() {
      this.sale.subtotal = this.sale.products
        .reduce((total, item) => {
          return total + parseFloat(item.subtotal);
        }, 0)
        .toFixed(2);
      this.calcDiscount();
    },
    calcProfitAmount() {
      this.sale.profit_amount =
        parseFloat(this.sale.total) - parseFloat(this.sale.purchase_amount);
    },
    changeQuantity(index) {
      this.sale.products[index].subtotal = (
        parseFloat(this.sale.products[index].sale_price) *
        parseFloat(this.sale.products[index].quantity)
      ).toFixed(2);
      this.calcSubtotal();
    },
    calcPrice(index) {
      if (
        this.sale.products[index].sale_price &&
        this.sale.products[index].primary_price_myanmar
      ) {
        this.sale.products[index].profit = parseFloat(
          this.sale.products[index].sale_price -
            this.sale.products[index].primary_price_myanmar
        );
        this.sale.products[index].margin = (
          (this.sale.products[index].profit /
            this.sale.products[index].sale_price) *
          100
        ).toFixed(2);
      } else {
        this.sale.products[index].margin = null;
        this.sale.products[index].profit = null;
      }
      this.changeQuantity(index);
    },
    calcAllProductPrice() {
      var data = [];
      this.sale.products.forEach((product, index) => {
        product.primary_price_myanmar = (
          (parseFloat(product.primary_price) *
            (parseFloat(this.sale.supplier_percentage) / 100) +
            parseFloat(product.primary_price)) *
          this.sale.money_price
        ).toFixed(0);
        this.calcPrice(index);
        data.push(product);
      });
      var dataString = JSON.stringify(data);
      setTimeout(() => {
        this.sale.products = [];
        this.sale.products = JSON.parse(dataString);
      }, 500);
    },
    changePrimaryPrice($event, index) {
      const supplier_percentage =
        parseFloat(this.sale.supplier_percentage) / 100;
      this.sale.products[index].primary_price_myanmar = parseFloat(
        (parseFloat($event) * supplier_percentage + parseFloat($event)) *
          this.sale.money_price
      );
      var string = JSON.stringify(this.sale.products);
      this.sale.products = [];
      this.sale.products = JSON.parse(string);
      this.calcPrice(index);
    },
    Put() {
      this.saving = true;
      this.sale.store = this.store.id;
      this.$axios
        .put("sale/", { data: this.sale, removed_ids: this.removed_ids })
        .then((res) => {
          this.sale = res.data;
          this.saleBeforeEdit = JSON.stringify(this.sale);
          this.removed_ids = [];
          this.sale.products.forEach((product) => {
            product.is_fulfilled =
              parseFloat(product.quantity) ===
              parseFloat(product.number_of_fullfilled);
          });
          this.calcDueAmount();
          setTimeout(() => {
            this.startEditing = false;
            this.adding = false;
            this.saving = false;
          }, 1);
          this.openPayment = false;
          this.$noti.display({
            title: "Success",
            color: "success",
            message: "Cool! Sale has been successfully added.",
          });
        })
        .catch((err) => {
          this.saving = false;
          this.$noti.display({
            message: err,
            title: "",
            status: "error",
            color: "error",
          });
        });
    },
    discardEditingSale() {
      this.sale = JSON.parse(this.saleBeforeEdit);
      setTimeout(() => {
        this.startEditing = false;
      }, 10);
    },
    generate() {
      this.showVoucher = true;
      setTimeout(() => {
        const node = document.querySelector(".invoice-body");
        domtoimage
          .toPng(node, {
            width: node.clientWidth * 5,
            height: node.clientHeight * 5,
            style: {
              transform: "scale(" + 5 + ")",
              transformOrigin: "top left",
            },
          })
          .then((dataUrl) => {
            const link = document.createElement("a");
            link.href = dataUrl;
            link.setAttribute(
              "download",
              this.sale.customer_name + "-voucher.png"
            ); //or any other extension
            document.body.appendChild(link);

            if (
              window.ReactNativeWebView &&
              window.ReactNativeWebView.postMessage
            ) {
              window.ReactNativeWebView.postMessage(dataUrl);
            }
            link.click();
            this.showVoucher = false;
          })
          .catch(() => {
            this.showVoucher = false;
          });
      }, 500);
    },
    changeIsSame() {
      if (this.sale.is_same_address) {
        this.sale.shipping_address = this.sale.address;
      } else {
        this.sale.shipping_address = {
          state: "",
          city: "",
          address: "",
          map: "",
        };
      }
    },
  },
  computed: {
    ...mapState(["isReady", "store"]),
  },
};
</script>
